import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-delivery',
  templateUrl: './delivery.component.html',
  styleUrls: ['./delivery.component.scss']
})
export class DeliveryComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    private lang: LanguageService,
    private vhComponent: VhComponent
  ) { }

  ngOnInit(): void {
  }
  gotoCarrierPartner() {
    if (this.vhAuth.checkMyPermission("shipping_enable_connect_partner")) this.router.navigate(['/sales/dashboard/manage/delivery/carrier-partner']);
    else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
  }
  gotoShippingBill() {
    this.router.navigate(['/sales/dashboard/manage/delivery/shipping-bill']);
  }
}
